<template>
    <div id="app">
        <v-app style="text-align: center">
            <v-layout>
                <v-flex>
                    <v-toolbar style="border-bottom-right-radius: 25px;border-bottom-left-radius: 25px;">
                        <v-card-actions>
                            <v-btn fab small left
                                   :color="this.$vuetify.theme.dark ? 'white' : '#424242'"
                                   style="opacity: 0.9"
                                   @click="SetNightMode">
                                <v-icon :color="this.$vuetify.theme.dark ? 'black' : 'white'">fa-moon-o</v-icon>
                            </v-btn>
                        </v-card-actions>

                        <v-spacer>Кака-я то фигня от Евстегнюгера Чачлыка</v-spacer>
                    </v-toolbar>
                    <v-btn fixed fab bottom right
                           color="#597da3"
                           @click="getAllPhotos"
                           style="opacity: 0.9">
                        <v-icon color="white">mdi-refresh</v-icon>
                    </v-btn>

                    <input ref="fileInput"
                           type="file"
                           style="display: none"
                           accept="image/*"
                           @change="onFileChange">
                    <v-speed-dial left bottom fixed
                                  v-model="setting_fab"
                                  direction="top"
                                  :open-on-hover="false"
                                  transition="slide-x-transition">
                        <template v-slot:activator>
                            <v-btn fab
                                   v-model="setting_fab"
                                   color="#597da3"
                                   style="opacity: 0.9">
                                <v-icon color="white">fa-bars</v-icon>
                            </v-btn>
                        </template>
                        <v-btn fab small
                               color="success"
                               @click="triggerFileUploads"
                               style="opacity: 0.9">
                            <v-icon color="white">mdi-plus</v-icon>
                        </v-btn>
                        <v-btn fab small
                               color="grey"
                               @click="dialog_settings = true"
                               style="opacity: 0.9">
                            <v-icon>fa-cog</v-icon>
                        </v-btn>

                    </v-speed-dial>
                    <div class="bg-light" style="border-radius: 25px; display:inline-flex; flex-wrap: wrap">
                        <v-col v-for="(photo, index) in photos" :key="index" style="">
                            <v-card style="border-radius: 25px;padding-bottom: 5px; padding-top: 5px" v-on="on" max-width="150" class="center-pic">
                                <v-img
                                        @mouseover="setModalInfo(photo.url, photo.text)"
                                        @click="dialog = true"
                                        class="center-pic"
                                        width="150px"
                                        height="150px"
                                        :src="photos.length > 1 ? getImgUrl(photo.url) : photo.url"
                                        style="border-radius: 25px"></v-img>
                            </v-card>
                            <div style="justify-content: center; border-radius: 25px;padding-top: 2px">
                                <v-btn rounded
                                       style="font-size: 1rem; right: 2px"
                                       @click="isNull(photo.like) ? setLikeOnPhoto_null(photo, index) : null">
                                    <v-icon :color="isNull(photo.like) ? 'grey': photo.like ? 'pink' : 'grey'">mdi-heart</v-icon>
                                    {{abbreviateNumber(photo.count_like)}}
                                </v-btn>
                                <v-btn rounded
                                       style="font-size: 1rem; left: 2px"
                                       @click="isNull(photo.like) ? setDisOnPhoto_null(photo, index) : null">
                                    <v-icon :color="isNull(photo.like) ? 'grey': photo.like ? 'grey' : '#4FC3F7'">mdi-heart-broken</v-icon>
                                    {{abbreviateNumber(photo.count_dis)}}
                                </v-btn>
                            </div>
                        </v-col>
                    </div>
                </v-flex>
            </v-layout>

            <v-dialog v-model="dialog_settings" fullscreen width="600">
                <v-card>
                    <v-toolbar style="border-bottom-right-radius: 25px;border-bottom-left-radius: 25px;">
                        <v-card-actions>
                            <v-btn depressed fab @click="dialog_settings = false"
                                   small left
                                   :color="this.$vuetify.theme.dark ? 'white' : '#c0c0c0'"
                                   style="opacity: 0.9">
                                <v-icon :color="this.$vuetify.theme.dark ? 'black' : 'white'">mdi-arrow-left</v-icon>
                            </v-btn>
                        </v-card-actions>
                        <v-spacer>Настройки</v-spacer>
                    </v-toolbar>

                    <v-container>
                        <v-subheader class="pl-0">Количество загружаемых фотографий: {{count}}</v-subheader>
                        <v-slider
                                v-model="count"
                                min="10"
                                max="35"
                        ></v-slider>
                        <v-switch label="жопа"></v-switch>
                        <v-switch label="жопа"></v-switch>
                        <v-switch label="жопа"></v-switch>
                        <v-switch label="жопа"></v-switch>
                        <v-switch label="жопа"></v-switch>
                        <v-spacer>
                            <v-btn style="border-radius: 10px">Сохранить</v-btn>
                        </v-spacer>
                    </v-container>

                </v-card>
            </v-dialog>

            <v-dialog v-model="dialog" fullscreen width="600">
                <v-card>
                    <v-toolbar style="border-bottom-right-radius: 25px;border-bottom-left-radius: 25px;">

                        <v-card-actions>
                            <v-btn depressed fab @click="dialog = false"
                                   small left
                                   :color="this.$vuetify.theme.dark ? 'white' : '#c0c0c0'"
                                   style="opacity: 0.8">
                                <v-icon :color="this.$vuetify.theme.dark ? 'black' : 'white'">mdi-arrow-left</v-icon>
                            </v-btn>
                        </v-card-actions>

                        <v-spacer>Фотография</v-spacer>

                    </v-toolbar>
                    <v-container>
                        <v-img
                                :src="photos.length > 1 ? getImgUrl(image) : image"
                                contain
                                style="border-radius: 5px; width: 85%;" class="center-pic"></v-img>
                        <br>
                        <p>{{text_modal}}</p>
                    </v-container>
                </v-card>
            </v-dialog>
            <v-snackbar
                    v-model="alert_upload"
                    bottom
                    :timeout="3000">
                {{ alert_upload_text }}
                <v-btn text
                       color="pink"
                       @click="alert_upload = false">
                    Закрыть
                </v-btn>
            </v-snackbar>
        </v-app>
    </div>
</template>
<script>
import axios from 'axios';


const url_flask = 'http://127.0.0.1:5000';

export const http = axios.create({
    baseURL: `${url_flask}/api`
});

import vk_connect from "@vkontakte/vk-connect";

// const app_id = 7194877;
const hash = window.location.search;
export default {
    name: 'app',
    data() {
      return {
          setting_fab: false,
          token: '',
          name: '',
          current_id: null,
          menu_photo: '',
          image: 'photo-1.jpg',
          text_modal: 1,
          count: 20,
          photos: [],
          dialog: false,
          dialog_settings: false,
          msg: null,
          loading_service: true,
          alert_upload: false,
          alert_upload_text: 'Успешно'
      };
    },
    methods:{
        isNull(val) {
            return val === null;
        },
        getImgUrl(pic) {
            return url_flask + pic;
        },
        setModalInfo(p, t) {
            this.image = p;
            this.text_modal = t;
        },
        setLikeOnPhoto_null(photo, id){
             this.photos[id].count_like += 1;
             this.photos[id].like = true;
             this.setLikeDisApi(photo.id, 'like')
        },
        setDisOnPhoto_null(photo, id){
            this.photos[id].count_dis += 1;
            this.photos[id].like = false;
            this.setLikeDisApi(photo.id, 'dis')
        },
        SetNightMode(){
            this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
        },
        async getAllPhotos() {
            try {
                await http.get(`/getPhoto${hash}${hash ? '&' : '?'}count=${this.count}`).then((res) => {
                    this.photos = res.data;
                });
            } catch {
                this.photos = [{
                    url: require('../assets/404-page-not-found.png'),
                    id: null,
                    like: true,
                    count_like: -1,
                    count_dis: -1,
                    uuid: 123,
                    text: 'Если ты это видишь, значит возникли технические шоколадки'
                }];
                this.alert_upload_text = 'Ошибка при обновлении.';
                this.alert_upload = true;
            }
        },
        setLikeDisApi(id, where){
            http.get(`/setPhotoLikeDis${hash}${hash ? '&' : '?'}id=${id}&${where}=1`)
                .then((res) => {
                    this.msg = res;
                })
                .catch(() => {

                });
        },
        abbreviateNumber(value) {
            let newValue = value;
            if (value >= 1000) {
                let suffixes = ["", "k", "m", "b","t"];
                let suffixNum = Math.floor( (""+value).length/3 );
                let shortValue = '';
                for (var precision = 2; precision >= 1; precision--) {
                    shortValue = parseFloat( (suffixNum != 0 ? (value / Math.pow(1000,suffixNum) ) : value).toPrecision(precision));
                    var dotLessShortValue = (shortValue + '').replace(/[^a-zA-Z 0-9]+/g,'');
                    if (dotLessShortValue.length <= 2) { break; }
                }
                if (shortValue % 1 != 0)  shortValue = shortValue.toFixed(1);
                newValue = shortValue+suffixes[suffixNum];
            }
            return newValue;
        },
        triggerFileUploads(){
            this.$refs.fileInput.click()
        },
        async onFileChange(event){
            let file = event.target.files[0];
            let data = new FormData();
            data.append("photo", file);

            try {
                await  fetch(`${url_flask}/api/UploadImage${hash}`, {method: "POST", body: data});
                this.alert_upload_text = 'Фотография загруженаю.';
            } catch {
                this.alert_upload_text = 'Ошибка при загрузке.';
            } finally {
                this.alert_upload = true;
            }

        }
    },

        mounted(){
            vk_connect.sendPromise("VKWebAppGetUserInfo", {}).then(r => {
              this.name = r.first_name + ' ' + r.last_name;
              this.current_id = r.id;
              this.menu_photo = r.photo_max_orig;

            })
            // vk_connect.sendPromise("VKWebAppGetAuthToken", { app_id, scope: "friends" }).then(r => r.access_token).then(access_token => {this.token = access_token});

        },
    created() {
        this.getAllPhotos();
    }
}


</script>

<style>
  .center-pic{
      display:block;
      margin:auto;
  }

  body::-webkit-scrollbar {
      display: none;
      width: 0;
  }

</style>