import Vue from 'vue'
import VueRouter from 'vue-router'
import HelloWorld from '../components/HelloWorld.vue'
import Settings from '../components/Settings.vue'
Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: HelloWorld
  },
  {
    path: '/settings',
    name: 'Settings',
    component: Settings
  },
];

const router = new VueRouter({
  routes
});

export default router
